//Mobile First 320px

.grid-container {
    display: grid;
    //max-width: 800px; //?
    grid-template-columns: 100%;
    grid-template-rows: auto;
    //grid-row-gap: 1px;
    //grid-column-gap: 1px;
    //grid-gap: 15px 16px; row first then column
    grid-template-areas:
    "header"
    "navigation"
    "main";
}

.header {
    grid-area: header;
    }
.navigation {
    grid-area: navigation;
    //background-color: rgba(80, 30, 137, 0.989);
    //grid-column: 1 / span 1;
    
    }
.main {
    grid-area: main;
    display: grid;
    }

//Tablet Size
@media (min-width: 768px) {
    
    .grid-container {
        grid-template-columns: 55px 1fr;
        grid-template-areas:
        "header header"
        "navigation main";
        }
    
    main {
        grid-template-columns: 50%;
    }
    
    #traffic, .social {
        grid-column: 1 / span 2;
        }
    
    .daily, #members, .message {
        grid-column: 1 / span 1;
        }
    
    .mobile, .activity, #settings {
        grid-column: 2 / span 1;
        }
    
    
    .widget-container-full {
        width: 90vw;
        }
    .widget-container-half {
        width: 45vw;
        }
    
    
    
    
    
    
    
    }



//Desktop Size
@media (min-width: 1024px) {
    .grid-container {
        grid-template-columns: 55px 1fr;
        grid-template-rows: 10%;
        grid-template-areas:
        "header header"
        "navigation main";
        }


}

.header {
    display: grid;
    //grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
}
    
.navigation {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}


